.info {
    display: flex;
    justify-content: flex-end;
    margin: 10px 0 10px 10px;
}

.info-item {
    margin-left: 10px;
}

.items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid navajowhite;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 5px;
}
.status {
    display: flex;
    justify-content: space-around;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 1px;
    border-bottom-style: dashed;
    border-bottom-color: rgb(155, 120, 120);
}

.status:hover {
    background-color: lightgray;
    color: white;
    cursor: grab;
}

.item {
    width: 100px;
}

.container {
    width: 800px;
}